.bar-chart-container {

}
.bar-chart-container__bar {
    height: 28px;
    border-radius: 2px;
    margin: 2px 0;
    background-color: #3c424f;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    padding-left: 10px;
    color: #9fa6b7;
    -webkit-transition: color 0.15s;
    -o-transition: color 0.15s;
    transition: color 0.15s;
    font-family: "OpenSans", san-serif;
    font-weight: bold;
    overflow: visible;
    white-space:nowrap;
}

.bar-chart-container__bar:hover {
    background-image: -webkit-gradient(linear, left bottom, left top, from(#7f8caa), to(#9fa6b7));
    background-image: -webkit-linear-gradient(bottom, #7f8caa, #9fa6b7);
    background-image: -o-linear-gradient(bottom, #7f8caa, #9fa6b7);
    background-image: linear-gradient(to top, #7f8caa, #9fa6b7);
    color: #FFFFFF;
}


