.statistics-container {
    height: 350px;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
}
.statistics-box {
    background-color: #21252b;
    -ms-flex-positive: 1;
        flex-grow: 1;
    padding: 0 25px;
    border: solid 1px #000000;
}
.statistics-box__header {
    width: 240px;
    width: 100%;
    height: 24px;
    font-family: OpenSans;
    font-size: 18px;
    font-weight: 800;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: justify;
    color: #5c636f;
    font-weight: bold;
    padding: 12px;
}

.statistics-box__element {
    width: 360px;
    height: 24px;
    font-family: OpenSans;
    font-size: 14px;
    font-weight: 800;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    color: #9fa6b7;
    font-weight: bold;
    background-color: #5c636f;
    padding: 12px;
    border-radius: 6px;
}

.statistics-box__element:active {
    background: -webkit-gradient(linear, left top, right top, from(#ba5375), to(#e86a73));
    background: -webkit-linear-gradient(left, #ba5375, #e86a73);
    background: -o-linear-gradient(left, #ba5375, #e86a73);
    background: linear-gradient(to right, #ba5375, #e86a73);
    color: #ffffff;
}

.time-statistics {
    -ms-flex-positive: 5;
        flex-grow: 5;
}




